.chat-wrapper {
  width: 100%;
  height: 100vh;
  position: relative;
}

.chat__header {
  height: 60px;
  min-height: 60px;
  min-width: 180px;
  width: 100%;
  padding: 5px;

  display: flex;
  position: relative;
  top: 0px;
  background-color: #ecb22e;
  /* border-bottom: 1px solid rgba(0,0,0,0.3); */
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.3);
}
.chat__header-title {
  width: calc(100% - 240px);
  height: 100%;
  min-width: 100px;
  font-size: 24px;
  font-weight: 700;
  overflow: hidden;

  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
}
.chat__header-title span {
  position: relative;
  left: 15px;
}
.chat__header-additional {
  width: 240px;
  height: 100%;
  min-width: 80px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.chat__header-search {
  width: 200px;
  height: 100%;
  padding: 7px 0px;
  position: relative;
}
.chat__header-search input {
  width: 100%;
  min-width: 40px;
  height: 100%;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 7px;
  font-size: 16px;
  color: black;
  padding-left: 35px;
  padding-top: 4px;
}
.chat__header-search input::placeholder {
  color: rgba(0, 0, 0, 0.25);
  font-weight: 800;
}
.chat__header-search i {
  position: absolute;
  top: 16px;
  left: 12px;
  font-size: 18px;
  font-weight: 600;
  opacity: 0.25;
}
.chat__header-more {
  width: 35px;
  padding-left: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 26px;
  font-weight: 500;
  color: black;
  opacity: 0.7;
}

.chat__body {
  width: 100%;
  height: calc(100% - 140px);
  min-height: 100px;
  border-left: 1px solid #eef2f4;
  flex-grow: 1;
  padding: 15px 0px;
  overflow: auto;
  position: relative;
}
.owner {
  flex-direction: row-reverse;
}
.chat-msg {
  display: flex;
  padding: 0 20px 30px;
}
.chat-msg-profile {
  flex-shrink: 0;
  margin-top: auto;
  margin-bottom: -20px;
  position: relative;
}
.chat-msg-img {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  object-fit: cover;
  max-width: 100%;
}
.chat-msg-date {
  position: absolute;
  left: calc(100% + 12px);
  bottom: 0;
  font-size: 12px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.3);
  white-space: nowrap;
}
.owner .chat-msg-date {
  left: calc(-100% - 12px);
}
.chat-msg-content {
  margin-left: 12px;
  max-width: 70%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.owner .chat-msg-content {
  align-items: flex-end;
  margin-left: 0px;
  margin-right: 12px;
}
.chat-msg-text {
  background-color: rgb(241, 242, 246);
  color: rgb(39, 51, 70);
  padding: 15px;
  border-radius: 20px 20px 20px 0;
  line-height: 1.5;
  font-size: 14px;
  font-weight: 500;
}
.owner .chat-msg-text {
  /* background-color: rgb(0, 134, 255); */
  background-color: #d696bb;
  color: #fff;
  border-radius: 20px 20px 0 20px;
}
.chat-msg-text + .chat-msg-text {
  margin-top: 10px;
}
.chat-msg-text img {
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
}

.chat__enter {
  width: calc(100% - 40px);
  height: 44px;
  min-height: 44px;
  margin: 18px;
  background-color: white;
  border: 2px solid rgba(27, 34, 46, 0.5);
  border-radius: 8px;
  z-index: 2;
  position: relative;
  bottom: 0px;
  display: flex;
}
.chat__enter-option {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  min-width: 38px;
  font-size: 22px;
  color: red;
  font-weight: 700;
}
.main-option {
  position: relative;
  border-right: 2px solid rgba(27, 34, 46, 0.5);
}
.main-option input {
  position: absolute;
  pointer-events: none;
  top: 0px;
  left: 0px;
  opacity: 0;
}
.sub-option {
  position: absolute;
}
.chat__enter-submit {
  right: 4px;
  top: 9px;
}
.chat__enter-input {
  width: calc(100% - 40px);
  height: 100%;
  padding-right: 40px;
}
.chat__enter-input input {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 3px 10px;
  padding-top: 5px;
  font-size: 16px;
  border: none;
}
